@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:300,400,500);
.uploadArea {
  width: 40%;
  height: 100%;
  border-radius: 4px;
  background-color: red;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-flow: column;
  cursor: pointer;
  background-size: 'cover';
  background-position: 'center center';
  background-repeat: 'no-repeat'; }
  .uploadArea__icon .arrow-upload {
    color: white;
    font-size: 100px; }
  .uploadArea__title {
    height: 50px;
    width: 60%;
    text-align: center; }
  @media screen and (max-width: 768px) {
    .uploadArea {
      height: 40%;
      width: 100%; } }

.itemsList {
  width: 60%;
  height: 100%;
  padding: 10px;
  overflow: hidden;
  overflow-y: scroll; }
  .itemsList::-webkit-scrollbar {
    display: none;
    width: 0px;
    background: transparent; }
  .itemsList::-webkit-scrollbar-thumb {
    display: none;
    background: #FF0000; }
  .itemsList .items {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    list-style-type: none; }
    .itemsList .items a {
      color: white; }
    .itemsList .items .itemInfo {
      width: 100%;
      margin: 0 0 5px 0;
      padding: 5px 5px 5px 10px;
      display: flex;
      justify-content: space-between;
      align-items: center; }
      .itemsList .items .itemInfo:hover {
        background-color: #fff;
        color: grey;
        border-radius: 4px; }
        .itemsList .items .itemInfo:hover a {
          color: grey; }
  @media screen and (max-width: 768px) {
    .itemsList {
      width: 100%;
      height: 60%; }
      .itemsList .itemInfo {
        color: grey;
        border-radius: 4px;
        background-color: #fff; } }

.item {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  flex-flow: column wrap;
  position: relative; }
  .item .back-nav {
    width: 30px;
    height: 30px;
    border-radius: 4px;
    position: absolute;
    top: 0;
    left: 0; }
    .item .back-nav a {
      position: fixed;
      width: 30px;
      height: 30px;
      color: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      text-decoration: none; }
    .item .back-nav:hover {
      background-color: #fff; }
      .item .back-nav:hover a {
        color: grey; }
  .item .title {
    height: 30px;
    width: 100%;
    padding: 5px 0;
    font-weight: bold;
    text-align: center; }
  .item .description {
    padding: 15px;
    width: 100%;
    min-height: 50px;
    text-align: center; }
  @media screen and (max-width: 768px) {
    .item .back-nav a {
      color: grey;
      border-radius: 4px;
      background-color: #fff; } }

html {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-size: 14px;
  font-weight: 400;
  background: #fff;
  color: #515151;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Roboto Condensed', sans-serif; }

#root {
  display: flex;
  align-items: center;
  justify-content: center; }

button, input, optgroup, select, textarea {
  font-family: 'Fira Sans', sans-serif; }

*,
*:before,
*:after {
  box-sizing: border-box; }

/* Clearfix class */
.group:after {
  content: "";
  display: table;
  clear: both; }

#root {
  height: 100%; }

a {
  text-decoration: none;
  color: #ffe4e4; }
  a:hover {
    text-decoration: underline; }

h1 {
  margin: 0;
  margin-right: .5em;
  font-size: 24px;
  font-weight: 700;
  color: #515151; }

input::-ms-clear {
  display: none; }

input[type='number'] {
  -moz-appearance: textfield; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none; }

::-webkit-input-placeholder {
  color: rgba(165, 165, 165, 0.57); }

::-ms-input-placeholder {
  color: rgba(165, 165, 165, 0.57); }

::placeholder {
  color: rgba(165, 165, 165, 0.57); }

:-ms-input-placeholder {
  color: rgba(165, 165, 165, 0.57); }

::-moz-selection {
  color: white; }

::selection {
  color: white; }

.App {
  width: 700px;
  height: 300px;
  border-radius: 4px;
  color: white;
  background-color: #a5a5a5;
  box-shadow: 2px 2px 10px #a5a5a5;
  display: flex;
  justify-content: flex-start;
  align-items: center; }
  @media screen and (max-width: 768px) {
    .App {
      width: 100%;
      height: 100%;
      flex-flow: column; } }

